button {
    outline: none;
    border: none;
    cursor: pointer;
}
.habit {
    font-size: 2.5rem;
    padding: 0.5em;
    list-style: none;
}

.habit-count {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    margin: 0 0.5em 0 0.2em;
    background-color: darkseagreen;
    color: white;
    border-radius: 50%;
}

.habit-button {
    font-size: 2.5rem;
    margin: 0 0.1em;
    background-color: transparent;
}
.habit-button:hover {
    opacity: 0.8;
}

.habit-increase,
.habit-decrease {
    color: goldenrod;
}

.habit-delete {
    color: rgb(255, 98, 80);
}
